'use client';

import { useState } from "react";
import Image from "next/image";
import logo from "../Images/482548321_3851477701771516_6987305829381938546_n.png"; // Use your logo

export default function Login() {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");

  const handleLogin = (e) => {
    e.preventDefault();
    // Handle login logic here
    console.log({ username, password });
  };

  return (
    <div className="bg-gradient-to-r from-green-300 to-green-500 h-screen flex items-center justify-center">
      <div className="w-full max-w-md bg-white p-8 rounded-3xl shadow-lg">
        {/* Logo */}
        <div className="flex justify-center mb-8">
          <Image src={logo} width={120} height={120} alt="logo" />
        </div>
        
        <h2 className="text-3xl font-bold text-center text-green-600 mb-6">Login to Recycle</h2>
        
        <form onSubmit={handleLogin} className="space-y-6">
          {/* Username Field */}
          <div>
            <label htmlFor="username" className="block text-sm font-medium text-gray-700">Username</label>
            <input 
              type="text" 
              id="username" 
              name="username" 
              className="w-full mt-2 p-4 bg-gray-50 border-2 border-gray-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-green-400" 
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              required
            />
          </div>

          {/* Password Field */}
          <div>
            <label htmlFor="password" className="block text-sm font-medium text-gray-700">Password</label>
            <input 
              type="password" 
              id="password" 
              name="password" 
              className="w-full mt-2 p-4 bg-gray-50 border-2 border-gray-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-green-400" 
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
            />
          </div>

          {/* Submit Button */}
          <button 
            type="submit" 
            className="w-full bg-green-600 text-white py-3 rounded-xl font-semibold hover:bg-green-700 transition duration-200"
          >
            Login
          </button>
        </form>

        {/* Forgot Password and Sign Up Links */}
        <div className="text-center mt-4">
          <a href="#" className="text-green-500 hover:text-green-600 text-sm">Forgot Password?</a>
        </div>
        <div className="text-center mt-2">
          <p className="text-sm text-gray-600">
            Don't have an account? 
            <a href="#" className="text-green-500 hover:text-green-600"> Sign up</a>
          </p>
        </div>
      </div>
    </div>
  );
}
